@import "../../../../../index.scss";

.dependencytask {
  width: 50%;
  background-color: #f8f8f8;
  padding: 8px 10px 0px 14px;

  // pointer-events: none;
  .taskdurationtext {
    color: $SecondaryTextColor;
  }

  .show-order {
    display: flex;
    padding-top: 11px;
    flex-wrap: wrap;

    div {
      background: red;
      margin: 2px;
      padding: 6px;
      font-size: 13px;
      color: #fff;
      border-radius: 6px;
    }
  }

  .startendtime {
    display: flex;
    margin: 10px 0px 12px 0px;

    .starttime {
      width: 82px;
    }

    .endtime {
      width: 82px;
      padding-left: 10px;
    }
  }

  .dcptext {
    color: $SecondaryTextColor;
    margin-bottom: 10px;
  }

  .togglerparent {
    width: 80px;
    height: 22px;
  }

  .selectdeptasktext {
    margin: 18px 0px 10px 0px;
  }

  .dependencysearchbar {
    width: 100%;
  }

  .tasklist_addtask {
    padding: 10px 0px 10px 4px;
    font-size: 12px;
    display: flex;
    cursor: pointer;

    &:hover {
      background-color: #f8f8f8;

      .tick {
        cursor: pointer;
        width: 13px;
      }
    }

    .task_no_wing {
      width: 25%;

      .taskno {
        color: #000000;
      }

      .wing {
        color: $SecondaryTextColor;
        padding-top: 3px;
      }
    }

    .task_name_details {
      width: 64%;

      .task_name {
        color: #000000;
      }

      .task_details {
        padding-top: 3px;
        color: $SecondaryTextColor;
      }
    }

    .tick {
      width: 13px;
      height: 13px;
      align-self: center;
    }
  }

  .bottomline_addtask {
    border-top: solid 1px #e7e8f7;
  }
}