@import "../../../../../index.scss";

.turnequpfleet {
  width: 100%;
  color: $SecondaryTextColor;
  display: flex;

  .turntype {
    width: 30%;

    .turntypedrop {
      padding: 10px 15px 0 0;
      color: #000000;
    }
  }

  .plannedTAT {
    width: 25%;

    .plannedTATvalue {
      color: #000000;
      padding: 10px 0px 0px 0px;
    }
  }

  .equptype {
    width: 25%;

    .equptypedrop {
      padding: 10px 16px 0 0;
      color: #000000;
    }
  }

  .fleetype {
    width: 20%;

    .fleettypeinput {
      padding: 10px 0px 0 0;
      color: #9b9b9b;
    }
  }
}