@import "../../../../../index.scss";
$TurnManagementMain:65%;
$TurnManagementManage:35%;
$headerHeight:26px;
$headerStartPosition:21px;
$TurnManagementMain:65%;


.TemplateViewHeader {
   margin: 10px 0;
   margin-top: $headerStartPosition;
   width: $TurnManagementMain;
   height: $headerHeight;
   overflow: hidden;

   .HeaderText {
      padding-left: 20px;
      color: $SecondaryTextColor;
      font-weight: bold;
      opacity: 0.7;
   }
}

.TemplateTaskList {
   width: $TurnManagementMain;
   height: 100%;
   background-color: #ffffff;
   border: solid 1px #e7e8f7;
   border-radius: 10px;
   padding: 15px 29px 0px 20px;

   .TotalTask_addTask {
      @include flex(flex-start);
      margin: 20px 0px 7px 0px;

      .TotalTask {
         flex: 1;
         color: $SecondaryTextColor;
      }

      .addTask {
         flex: 1;
         @include flex (flex-end);
      }
   }

   .searchcontainer {
      width: 100%;
      border-top: solid 1px #e7e8f7;
      background-color: #f8f8f8;
      padding: 7.5px 11px 8px 9px;

      .searchbar {
         width: 100%;
         border: solid 1px #f0f0f0;
         background-color: #ffffff;
      }
   }

   .TaskHeading {
      display: flex;
      font-size: 14px;
      font-weight: bold;

      // text-align: center;
      .Task_wing {
         width: 20%;
      }

      .Taskname_Description {
         width: 35%;
      }

      .Departmentname {
         width: 15%;
      }

      .Taskstart {
         width: 15%;
      }

      .Taskend {
         width: 11%;
      }
   }

   .assignedTaskcontainer {
      padding: 13px 0px 10px 0px;
      width: 100%;
      font-size: 12px;
      cursor: pointer;
      @include flex(flex-start);

      &:hover {
         background-color: #f8f8f8;

         .Taskdelete {
            display: block;
         }
      }

      .Task_wing {
         width: 20%;

         .Taskno {
            color: #000000;
         }

         .wing {
            color: $SecondaryTextColor;
            padding-top: 3px;
         }
      }

      .Taskname_Description {
         width: 35%;

         .Taskname {
            color: #000000;
         }

         .TaskDescription {
            color: $SecondaryTextColor;
            padding-top: 3px;
         }
      }

      .Departmentname {
         width: 15%;
         color: $SecondaryTextColor;
      }

      .Taskstart {
         width: 15%;
         color: #999999;
      }

      .Taskend {
         width: 11%;
         color: #999999;

      }

      .Taskdelete {
         display: none;
      }
   }

   .line {
      border: solid 1px #ededed;
      width: 100%;
   }
}