@import "../../../../../index.scss";

.color-createtemplate {
    color: $SecondaryTextColor;
}

.margin-top-createtemplate {
    margin-top: 10px;
}

.CreateTurnTemplateHeader {
    margin: 10px 0;
    margin-top: 21px;
    width: 65%;
    height: 26px;
    overflow: hidden;

    .CreateTurnTemplateHeaderText {
        padding-left: 20px;
        color: $SecondaryTextColor;
        font-weight: bold;
        opacity: 0.7;
    }
}

.headercreatetemplate {
    width: 100%;

    .Temp-Station {
        display: flex;
        width: 100%;
        padding: 17px 0px 10px 0px;

        .Temp {
            width: 50%;
        }

        .station {
            width: 50%;
            margin-left: 15px;

        }
    }

    .Turn-planned-equptype-fleetcat {
        display: flex;
        margin-bottom: 23px;

        .Turn {
            width: 35%;
        }

        .planned {
            width: 20%;
            margin: 0px 16px 0px 13px;

        }

        .equptype {
            width: 20%;
            margin: 0px 17px 0px 13px;

        }

        .fleetcat {
            width: 25%;
        }
    }
}