.tab-list {
  // width: 25%;
  border-bottom: 1px solid #ccc;
  padding-left: 0;
  white-space: nowrap;
  overflow-x: scroll;

  &::-webkit-scrollbar {
    display: none;
  }
}

.tab-list-item {
  display: inline-block;
  list-style: none;
  margin-bottom: -1px;
  padding: 0.5rem 0.75rem;
  width: 80px;
  white-space: nowrap;
  user-select: none;
}

.tab-list-active {
  // width: 25%;
  background-color: white;
  border-bottom: solid red;
  border-width: 1px 1px 3px 1px;
  color: red;
}