@import '../../../../../index.scss';

$headerHeight: 22px;

.TaskHeading {
  width: 100%;
  margin: 19px 0px 11px 0px;
  display: flex;
  font-size: 12px;
  color: $SecondaryTextColor;
}

.TaskList {
  margin-top: 5px;
  width: 55%;
  color: $SecondaryTextColor;
}

.task-search-header {
  position: relative;
  // width: 45%;

  .task-search-icon {
    position: absolute;
    width: 18px;
    height: $headerHeight;
    line-height: $headerHeight;
    color: #999999;
    left: 7px;
  }

  .searchbartask {
    width: 100%;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    padding: 6px 0px 6px 29px;
    margin-left: auto;
    font-size: 12px;
    height: 22px;
  }
}

.task1 {
  margin-bottom: 19px;
}
