@import "../../../../../index.scss";

.Task {
    width: 100%;
    background-color: #ffffff;
    cursor: default;
}

.Task-item {
    border-radius: 0px;
    border-bottom: solid 1px #f0f0f0;
    padding: 12px 19.2px 7px 15px;
    display: flex;
    cursor: pointer;

    &:hover {
        background-color: #f8f8f8;
    }

    &:hover .delete-icon-div {
        display: flex;
        position: absolute;
        padding: 0.2% 0px 0px 1%;

        cursor: pointer;
    }

    .TaskNumber {
        width: 35%;
        font-size: 12px;
        color: #000000;
    }

    .TaskDescription {
        width: 45%;
    }

    .TaskCategory {
        width: 20%;
        font-size: 12px;
        color: $SecondaryTextColor;
        display: flex;
        justify-content: flex-end;


    }

    .delete-icon-div {
        display: none;
    }
}

.Taskrow2 {
    font-size: 12px;
    color: $SecondaryTextColor;
    padding-top: 3px;
}




