@import "../../../../../index.scss";

.TaskBody {
    width: 100%;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    padding: 15px;
}

.Textcolor {
    opacity: 0.5;
    font-size: 12px;
    color: #000000;
    margin-bottom: 7px;
}

.Textpadding {
    padding-top: 16px;
}

.buttondesign {
    display: flex;
    margin-top: 13px;
    flex-flow: row-reverse;
}

.inputbox {
    width: 100%;
    border-radius: 5px;
    border: solid 1px #d3d3d3;
    background-color: #ffffff;
    padding: 10px 0px 12px 19px;
    color: #000000;
    font-size: 12px;
}

.createTasks {
    margin: 15px 0px 10px 0px;
    color: $SecondaryTextColor;
    font-size: 12px;
}