@import "../../../../../index.scss";


// header
$TurnManagementMain:65%;
$TurnManagementManage:35%;
$headerHeight:22px;
$headerStartPosition:21px;


.TurnManagementHeader {
   margin: 10px 0;
   margin-top: $headerStartPosition;
   width: $TurnManagementMain;
   height: $headerHeight;
   overflow: hidden;
}

.TurnManagementwrap {
   display: flex;
}

.TurnManagementText {
   color: $SecondaryTextColor;
   opacity: 0.7;
   font-size: 14px;
   flex: 1;
   height: $headerHeight;
   line-height: $headerHeight;
   padding-left: 20px;
   font-weight: bold;

}

.Turn-header-right-content {
   flex: 3;
   display: flex;
   justify-content: flex-end;
}

.turn-search-header {
   position: relative;

   .turn-search-icon {
      position: absolute;
      width: 18px;
      height: $headerHeight;
      line-height: $headerHeight;
      color: #999999;
      left: 7px;
   }

   .TurnManagementSearchBar {
      width: 171px;
      border-radius: 10px;
      border: solid 1px #f0f0f0;
      background-color: #ffffff;
      height: $headerHeight;
      font-family: Roboto;
      font-size: 12px;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.17;
      letter-spacing: normal;
      text-align: left;
      color: #999999;
      padding-left: 28px;
   }
}

.turn-header-create {
   position: relative;

   .turn-create-icon {
      position: absolute;
      width: 12px;
      height: 22px;
      line-height: 26px;
      font-weight: bold;
      color: #fff;
      left: 15px;
      font-size: 12px;
   }

   .CreateTurnTemplate {
      width: 151px;
      border-radius: 20px;
      margin-left: 5px;
      color: #ffffff;
      background-color: $textColor;
      height: $headerHeight;
      font-size: 12px;
      font-family: "Roboto";
      font-stretch: normal;
      font-style: normal;
      line-height: #{$headerHeight}-3;
      letter-spacing: normal;
      text-align: left;
      padding-left: 28px;
   }
}