@import "../../../../../index.scss";

.task {
  width: 50%;
  height: 100%;
  border-right: solid 1px #e7e8f7;

  .searchcover {
    background-color: #f8f8f8;
    border-top: solid 1px #e7e8f7;
    padding: 7.5px 16px 8px 9px;

    .searchbox {
      width: 100%;
    }
  }

  .eng_ground_sec {
    display: flex;
    align-content: flex-end;
    width: 100%;
    cursor: pointer;
    color: $PrimaryTextColor;

    &>div:hover {
      color: #000000;
    }
  }

  .bottomline {
    border-top: solid 1px #e7e8f7;
  }

  .isactive {
    background-color: rgba(241, 27, 54, 0.82);
    border-radius: 4px;

    .task_no_wing {
      width: 25%;

      .taskno {
        color: #fff;
      }

      .wing {
        color: #fff;
      }
    }

    .task_name_details .task_name {
      color: #fff;
    }

    .task_name_details .task_details {
      color: #fff;
    }
  }
}

.tasklist_addtask {
  padding: 10px 0px 10px 4px;
  font-size: 12px;
  display: flex;
  cursor: pointer;

  &:hover {
    background-color: #f8f8f8;

    .tick {
      width: 13px;
    }
  }

  &.isactive:hover {
    background-color: rgba(241, 27, 54, 0.82);
  }

  .task_no_wing {
    width: 25%;

    .taskno {
      color: #000000;
    }

    .wing {
      color: $SecondaryTextColor;
      padding-top: 3px;
    }
  }

  .task_name_details {
    width: 64%;

    .task_name {
      color: #000000;
    }

    .task_details {
      padding-top: 3px;
      color: $SecondaryTextColor;
    }
  }

  .tick {
    width: 13px;
    height: 13px;
    align-self: center;
  }

  .bottomline_addtask {
    border-top: solid 1px #e7e8f7;
  }
}