@import "../../../../../index.scss";

$shadowcolor: #000000;
$opacity: 0.5;
$smallfont: 12px;
$largerfont: 14px;
$whitebackground: #ffffff;

@mixin flex($content: center, $direction: row, $align: stretch) {
  display: flex;
  justify-content: $content;
  flex-direction: $direction;
  align-items: $align;
}

.CreateDepartmentText {
  margin: 15px 0 10px 0px;
  width: 100%;
  font-size: $smallfont;
  color: $SecondaryTextColor;
}

.DepartmentDetails {
  background-color: $whitebackground;
  border: solid 1px #f0f0f0;
  padding: 17px 9.2px 24px 15px;
  color: $shadowcolor;
  border-radius: 10px;

  .DepartmentText {
    opacity: $opacity;

  }

  .DepartmentInputelements {
    width: 100%;
    margin: 7px 15.2px 16px 0px;
    border-radius: 5px;
    border: solid 1px #d3d3d3;
    padding: 10px 0px 12px 19px;

    &:disabled {
      opacity: 0.9;
      cursor: not-allowed;
    }
  }


}