@import '../../../../../index.scss';

$smallfont: 12px;
$largerfont: 14px;
$shadowcolor: #000000;
$whitebackground: #ffffff;

.DepartmentlistText {
  color: $SecondaryTextColor;
  font-size: $smallfont;
  margin: 17px 0px 10px 0px;
  cursor: default;
}

.Departmentlist {
  width: 100%;
  border: solid 1px #f0f0f0;
  background-color: $whitebackground;
  margin-bottom: 18px;
  cursor: default;

  .Departmentlistwrap {
    @include flex(flex-start);
    padding: 13px 19.2px 10px 10.5px;
    border-bottom: solid 1px #ededed;

    &:hover {
      background-color: #f8f8f8;
    }

    &:hover .delete-icon-div {
      display: flex;
      position: absolute;
      padding: 0px 0px 0px 1%;

      cursor: pointer;
    }

    .Departmentid {
      width: 35%;
      color: $shadowcolor;
      font-size: $smallfont;
    }

    .Departmentname {
      width: 45%;
      padding-left: 15px;
      font-size: $smallfont;
    }

    .Departmentcode {
      width: 20%;
      color: $SecondaryTextColor;
      font-size: 12px;
      @include flex(flex-end) // &:hover{
        //     background-color:white;
        // };;
    }

    .delete-icon-div {
      display: none;
    }
  }
}
