@import "../../../../../index.scss";

$TurnManagementMain:100%;

.Flights {
   width: $TurnManagementMain;
}

.Flight_Box {
   width: 100%;
   background-color: #ffffff;
   border: solid 1px #e7e8f7;
   border-radius: 10px;
   display: flex;
   padding: 15px 20px 15px 20px;
   margin-bottom: 5px;
   cursor: pointer;

   &:hover {
      background-color: #f8f8f8
   }
}

.Flights_Details {
   width: 55%;
   display: flex;
   font-size: 14px;
   overflow: hidden;
}

.Flight_Number {
   width: 45%;
   color: $PrimaryTextColor;
   font-size: 14px;
}

.BaseBody {
   font-size: 12px;
   color: $SecondaryTextColor;
   padding-top: 8px;
}

.header {
   font-size: 14px;
   color: $SecondaryTextColor;
}

.Base_Details {
   width: 65%;
   color: #000000;
   //  font-weight: bold;
}

._Date_Time {
   display: flex;
   width: 45%;

}

._Date1 {
   width: 50%;
   justify-content: flex-end;

}

.displayflex {
   display: flex;
   padding-top: 8px;
}

._Time1 {
   margin-left: 10px;
}

.margin-left {
   margin-left: 20px;
}